@font-face {
    font-family: 'Сy Grotesk';
    src: local('Grotesk Wide Regular'), local('GroteskWideRegular'),
        url('../../fonts/GroteskWideRegular.woff2') format('woff2'),
        url('../../fonts/GroteskWideRegular.woff') format('woff'),
        url('../../fonts/GroteskWideRegular.ttf') format('ttf');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Cera Pro';
    src: local('CeraPro-Regular'),
        url('../../fonts/CeraPro-Regular.woff2') format('woff2'),
        url('../../fonts/CeraPro-Regular.woff') format('woff'),
        url('../../fonts/CeraPro-Regular.ttf') format('ttf');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

body, #first {
    overflow-x: hidden;
    min-height: 100vh;
    min-height: -webkit-fill-available;
}

body {
    font-family: 'Сy Grotesk', sans-serif;
}

.container {
	max-width: 1224px;
	width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.main_title {
	font-family: 'Сy Grotesk', sans-serif;
	font-size: 64px;
	font-weight: 400;
	line-height: 64px;
	letter-spacing: 0.01em;
	text-align: left;
}

.main-hdr {
    margin-top: 60px;
}

.main-hdr .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/**
* Burger Menu
*/

.burger-lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40px;
    gap: 8px;
    cursor: pointer;
}

.burger-line {
    width: 100%;
    height: 2px;
    background-color: #000;
}

.closemenu {
    display: none;
    margin: 0;
    transition: transform .3s ease;
    z-index: 999;
}

.closemenu:hover {
    transform: translateZ(180deg);
    cursor: pointer;
}

.burger-lines.active .closemenu {
    display: block;
}

.burger-lines.active .burger-line {
    display: none;
}