#intro {
    position: relative;
    overflow: visible;
}

.intro__top {
    display: flex;
    justify-content: space-between;
    margin-top: 132px;
    position: relative;
}

.intro__top-title {
    width: 100%;
    font-size: 80px;
    line-height: 100%;
    max-width: 480px;
    line-height: 101px;
}

.intro__top-left {
    display: flex;
    flex-direction: column;
    gap: 62px;
}

.intro__get_in_touch,
.more_projects_link {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 30px 62px;
    border: 1px solid #000;
    text-decoration: none;
    font-family: 'Cera Pro', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0.01em;
    width: 100%;
    max-width: 272px;
}

.intro__get_in_touch-text,
.more_projects_link-text {
    color: #000;
}

.intro__top-right {
    margin-right: 104px;
}

.intro__top-right-info {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    max-width: 322px;
}

.intro__top-right-more-link {   
    display: flex;
    align-items: center;
    gap: 12px;
    max-width: 148px;
    margin-top: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #040505;
    text-decoration: none;
    color: #000;
}

.intro__top-right-more-text {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.03em;
}

.intro__parallax {
    position: absolute;
    bottom: 0;
    left: 0;
}

.cube-wrap {
    position: absolute;
    width: 408px;
    right: calc( ((100vw - 1226px) / 2) + 278px);
    bottom: -136px;
}

.intro__parallax img {
    transform: rotate(3deg);
}

#areas {
    padding-top: 140px;
}

.areas_title {
    margin-bottom: 60px;
}

.areas-grid-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 40px;
    padding-bottom: 60px;
    border-top: 1px solid #E5E5E5;
}

.areas-grid-item:last-child {
    padding-bottom: 0;
}

.areas-heading {
    font-family: 'Сy Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    min-width: 291px;
}

.areas-heading sup {
    font-size: 13px;
    line-height: 13px;
    font-family: 'Cera Pro', sans-serif;
    top: -14px;
    margin-left: 12px;
}

.areas-heading .motion-sup {
    margin-left: 34px;
}

.areas-text {
    max-width: 300px;
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #040505;
    min-width: 312px;
}

#selected {
    padding-top: 150px;
}

.selected_title {
    margin-bottom: 60px;
}

.selected-grid {
    padding-top: 65px;
    border-top: 1px solid #EBEBEB;
}

.selected-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 65px;
}

.selected-left {
    width: 50%;
    max-width: 470px;
}

.selected-right {
    width: 50%;
    max-width: 600px;
}

.selected-icons {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    font-size: 0;
    margin-bottom: 35px;
}

.selected-heading {
    line-height: 150%;
    margin-bottom: 30px;
    font-size: 40px;
}

.selected-text {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #040505;
    margin-bottom: 35px;
}

.selected-tags {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 10px 25px;
}

.selected-tag {
    font-family: 'Сy Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
    color: #000;
}

.more_projects {
    display: flex;
    justify-content: center;
    margin-bottom: 65px;
}

.more_projects_link {
    padding: 30px 58px;
    width: auto;
    max-width: unset;
}

#mainpage-bottom-header {
    margin-bottom: 160px;
}

.feedback-heading {
    font-family: 'Сy Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 66px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    padding-bottom: 38px;
    margin-bottom: 230px;
    border-bottom: 2px solid #E5E5E5;
}

.main_footer {
    margin-bottom: 70px;
}

.footer-grid {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.copyright-item, .copyright-item a {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #040505;
}

.footer-contacts {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.footer-links ul {
    display: flex;
    align-items: center;
    gap: 26px;
    flex-wrap: wrap;
    list-style-type: none;
}

.footer-email {
    text-align: right;
}

.footer-email-link {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 39px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
}

.footer-links ul li a {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: #000000;
    text-decoration: none;
}

/**
*
* Burger Menu
*
*
**/


.main-hdr .container {
    position: relative;
}

.menu_open {
    width: 100%;
    max-width: 720px;
    height: 100%;
    padding: 40px 118px 40px 90px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
    background-color: #fff;
    display: none;
    transition: transform .3s ease;
    transform: translateX(100%);
}

.menu_open.moved:before {
    background: rgba(4, 5, 5, 0.03);
    content: '';
    top: 0;
    right: 100%;
    width: 200vw;
    height: 100vh;
    position: absolute;
}

.menu_open.opened {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.menu_open.moved {
    transform: translateX(0%);
}

.menu_main_list ul {
    list-style-type: none;
}

.menu_main_item {
    margin-bottom: 26px;
}

.menu_main_link {
    font-family: 'Сy Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
}

.web3-list {
    margin: 60px 0;
}

.web3-list .menu_main_link {
    font-size: 36px;
    line-height: 54px;
}

.menu_open_links {
    display: flex;
    flex-direction: column;
    gap: 23px;
}

.menu_open_email-link {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 39px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
}

.menu_open-socials {
    display: flex;
    gap: 15px;
}

.menu_open-social {
    font-size: 0;
    text-decoration: none;
}

.menu_open-mini-lbl {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 100%;
    margin-bottom: 7px;
}

.email-mini-lbl {
    margin-bottom: 15px;
}

.web-3-mini-lbl {
    margin-bottom: 5px;
}